<template>
<page>
    <header-title title="订单详情" backIcon="icon-allow"></header-title>
    <mt-field class="input-wrapper" label="姓名" disabled v-model="content.name"></mt-field>
    <mt-field class="input-wrapper" label="支付宝账号" disabled v-model="content.alipayid"></mt-field>
    <div class="selectImage">
     <Photo @selected="selectImage">
        <div class="item">
          <i v-if="!baseurl" class="icon icon-push"></i>
          <img v-else :src="baseurl" alt>
        </div>
      </Photo>
      </div>
    <mt-button type="primary" @click="commit">提交</mt-button>

</page>
</template>
<script>
import Photo from '@/common/pickImage'
import requset from '@/http'
import {Toast} from 'mint-ui'
import { getBuyerInfo, getSellerInfo, getConfirmPay, getConfirmRec, postBaseImage } from '@/http/api'

export default {
  components: {
    Photo
  },
  data () {
    return {
      content: {},
      baseurl: '',
      imagePath: ''
    }
  },
  methods: {
    async selectImage (url) {
      this.baseurl = url
      let {data} = await requset.post(`${postBaseImage}?type=image&file=${this.baseurl}`).then(res => {
        Toast(res)
      })
      this.imagePath = data
    },
    async commit () {
      if (!this.baseurl || !this.imagePath) {
        Toast('上传截图')
        return
      }
      requset.post(`${getConfirmPay}/${this.content.orderid}?picPath=${this.imagePath}`).then(res => {
        Toast(res)
      })
    }
  },
  created () {
    console.log(this.$route)
  },
  async beforeRouteEnter (to, from, next) {
    let url
    const info = to.params.info
    if (info.status === 2) {
      url = `${getSellerInfo}/${info.match}`
    } else {
      url = `${getBuyerInfo}/${info.match}`
    }
    let res = await requset.get(url)
    next(vm => {
      vm.content = res.data
    })
  }
}
</script>
<style lang='scss' scoped>
.input-wrapper /deep/ {
  margin-top: 3px;
  background-color: $item-color;
  color: $font-light-color;
  font-size: $font-size;
  input {
    text-align: right;
  }
  .mint-field-clear {
    display: none;
  }
  .mint-cell-value.is-link {
    color: $font-color;
  }
}
.selectImage {
  padding: 10px;
  .item {
    height: 50px;
    line-height: 50px;
    text-align: center;
    width: 50px;
    background-color: $item-color;
    i {
      font-size: 30px;
      color: $font-color;
    }
  }
}
</style>



